/* eslint-disable no-param-reassign */
import React, { useEffect, useState } from 'react';
import { Button, FormGroup, Row } from 'reactstrap';
import { useHistory } from 'react-router-dom';
import { connect } from 'react-redux';
import {
  Colxx,
  Separator,
} from '../../../../components/common/CustomBootstrap';

import Breadcrumb from '../../../../containers/navs/Breadcrumb';

import IntlMessages from '../../../../helpers/IntlMessages';
import { adminRoot } from '../../../../constants/defaultValues';

import {
  getHackathonByID,
  updateCurrentHackathon,
} from '../../../../redux/actions';

// const SelectQuestions = React.lazy(() =>
//   import(
//     /* webpackChunkName: "application-select-question" */ './components/SelectQuestions'
//   )
// );

const AddCustomQuestion = React.lazy(() =>
  import(
    /* webpackChunkName: "application-custom-question" */ './components/AddCustomQuestion'
  )
);

// const SubmitButton = React.lazy(() =>
//   import(
//     /* webpackChunkName: "application-submit-button" */ './components/SubmitButton'
//   )
// );

const Application = ({
  match,
  accessToken,
  updateAction,
  getDetailsAction,
  loading,
  hackathonDetails,
}) => {
  const history = useHistory();

  const { id } = match.params;

  if (!id) {
    history.goBack();
  }

  useEffect(() => {
    updateAction(id);
    getDetailsAction(accessToken, id);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const { application } = getHackathonByID(accessToken,id);
  const [values, setValues] = useState({
    id,
    application,
  });

  useEffect(() => {
    setValues({
      ...values,
      application: hackathonDetails.application,
    });
    console.log('UPDATED', hackathonDetails.application);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [hackathonDetails]);

  if (loading) return <div className="loading" />;

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.application" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>

      <Row className="justify-content-center">
        <Colxx xxs="12" md="8">
          <AddCustomQuestion
            id={id}
            formValues={values}
            setFormValues={setValues}
            accessToken = {accessToken}
          />
        </Colxx>
      </Row>

      <Row>
        <Colxx>
          <FormGroup className="text-center">
            <Button
              color="primary"
              className="m-3"
              onClick={() =>
                history.push(`${adminRoot}/challenges/sponsors/${id}`)
              }
            >
              <IntlMessages id="forms.previous" />
            </Button>
            <Button
              color="primary"
              className="m-3"
              onClick={() =>
                history.push(`${adminRoot}/challenges/faq/${id}`)
              }
            >
              <IntlMessages id="forms.next" />
            </Button>
          </FormGroup>
        </Colxx>
      </Row>
    </>
  );
};

const mapStateToProps = ({ authUser, hackathonData }) => {
  const { accessToken } = authUser;
  const { hackathonDetails, loading, error } = hackathonData;
  return { accessToken, hackathonDetails, loading, error };
};

export default connect(mapStateToProps, {
  updateAction: updateCurrentHackathon,
  getDetailsAction: getHackathonByID,
})(Application);
